import styled from "@emotion/styled";

export const TextBlock = styled.div`
    /* text-align: justify; */

    &:after {
        content: ".";
        visibility: hidden;
        display: block;
        height: 0;
        clear: both;
    }

    > * + * {
        margin-top: 1em;
    }

    > * + h3 {
        margin-top: 2em;
    }

    h2 {
        border-left: 1em solid dodgerblue;
        padding-left: .5em;
    }

    h3 {
        color: rgba(0, 0, 0, .5);
        font-size: .75em;
        line-height: 1;
        padding-bottom: .25em;
        border-bottom: 1px solid rgba(0, 0, 0, .1);

        a {
        }
    }

    p, ul {
        color: rgba(0, 0, 0, .5);
        font-size: .6em;
        line-height: 1.5;
    }

    ul {
        list-style: circle;
        padding-left: 1em;
    }

    a {
        border-bottom: 1px solid dodgerblue;

        &:hover {
            color: dodgerblue;
            /* background-color: rgba(0, 0, 0, .5); */
        }
    }

    strong {
        font-weight: bold;
    }

    i {
        font-style: italic;
    }

    kbd {
        background-color: #eee;
        font-family: 'Inconsolata', monospace;
    }
`;

const layoutWidth = 1280;
const layoutPadding = '1em';
export const ContentBlock = styled.div`
  padding: 0 ${layoutPadding};
  max-width: ${layoutWidth}px;
  margin: 0 auto;
`;

const imagesListSpacing = '.5em';
const imagesColumns = 4;
const ImagesList = styled.ul`
  line-height: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -${imagesListSpacing} -${imagesListSpacing} 0 0;

  > li {
    margin: ${imagesListSpacing} ${imagesListSpacing} 0 0;
    /* width: calc((${layoutWidth}px - 2 * ${layoutPadding} - ((${imagesColumns} - 1) * ${imagesListSpacing})) / ${imagesColumns}); */
  }

  img {
    display: block;
    width: 100%;
    max-height: 200px;
    transition: transform .2s;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, .25);

    &:hover {
      transform: scale(1.1);
    }
  }
`;
