export const ImagesGrid = styled.div`
    display: grid;
    /* grid-template-rows: repeat(8, 5vw); */
    /* grid-template-columns: repeat(4, 1fr); */
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 1em;

    img {
        display: block;
        width: 100%;
        height: 100%;
        /* width: 350px;
        height: 350px; */
        object-fit: cover;
    }
`;